import * as React from "react";
import styled from "styled-components";
import { Avatar } from "../components/avatar";
import { Card } from "../components/card";
import { PageProps, graphql } from "gatsby";
import { withPageLayout } from "../components/with-page-layout";

const AboutCard = styled(Card)`
  flex-direction: column;
  align-items: center;
  width: 60%;
`;

const AboutAvatar = styled(Avatar)`
  margin-bottom: 1rem;
  overflow-wrap: break-word;
`;

const AboutContent = styled.div`
  width: 100%;
  text-align: center;
  line-height: 1.5rem;
  padding: 0 2rem;
`;

function AboutPage({
  data,
}: PageProps<Queries.AboutPageQuery>): React.ReactElement<any, any> | null {
  if (!data?.file?.childImageSharp?.gatsbyImageData) return null;

  return (
    <React.Fragment>
      <AboutCard>
        <AboutAvatar image={data.file.childImageSharp.gatsbyImageData} />
        <AboutContent>
          Hey! My name is Sean Rodman. I am a single father, A software
          engineer, and an adult with ADHD. I write this blog to record my
          experiences with ADHD as a single father of a child with ADHD.
        </AboutContent>
      </AboutCard>
    </React.Fragment>
  );
}

export const pageQuery = graphql`
  query AboutPage {
    file(
      sourceInstanceName: { eq: "images" }
      relativePath: { eq: "about.jpg" }
    ) {
      childImageSharp {
        gatsbyImageData(width: 200, height: 200, layout: FIXED)
      }
    }
  }
`;

export default withPageLayout(AboutPage);
